@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
  -webkit-align-items: center;
          align-items: center;
  background-image: linear-gradient(to right, transparent, silver, transparent);
  display: -webkit-flex;
  display: flex;
  font-family: "Permanent Marker", cursive;
  font-size: 2rem;
  -webkit-justify-content: center;
          justify-content: center;
}

.todd-img {
  height: 80px;
  position: relative;
  top: 12px;
}

@media screen and (max-width: 760px) {
  .Header {
    font-size: 1.2rem;
  }

  .todd-img {
    height: 60px;
    position: relative;
    top: 12px;
  }
}

@media screen and (max-width: 500px) {
  .Header {
    font-size: 0.8rem;
  }

  .todd-img {
    height: 40px;
    position: relative;
    top: 8px;
  }
}

.Player {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(147, 147, 147);
  color: white;
  display: -webkit-flex;
  display: flex;
  font-size: 32px;
  height: 80px;
  margin: 10px 0;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 20px;
}

.pic {
  height: 80px;
}

@media screen and (max-width: 760px) {
  .Player {
    font-size: 22px;
  }
}

@media screen and (max-width: 500px) {
  .Player {
    font-size: 16px;
    height: 60px;
  }
  .pic {
    height: 60px;
  }
}

.Standings {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.current-standings {
  background-color: silver;
  height: 70vh;
  opacity: 0.8;
  overflow: auto;
  padding: 0 5%;
  width: 60%;
}

.current-standings-header {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(84, 84, 84);
  color: white;
  display: -webkit-flex;
  display: flex;
  font-size: 22px;
  height: 50px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
  padding-left: 12px;
  padding-right: 32.5%;
}

@media screen and (max-width: 1300px) {
  .current-standings {
    width: 70%;
  }
}

@media screen and (max-width: 760px) {
  .current-standings {
    height: 80vh;
    width: 80%;
  }

  .current-standings-header {
    font-size: 16px;
  }
}

.App {
  background: url(/long-shot-league/static/media/court.58e6fc11.jpg) center no-repeat;
  background-size: cover;
  display: grid;
  grid-template-rows: 140px 1fr;
  height: 100vh;
}

@media screen and (max-width: 760px) {
  .App {
    grid-template-rows: 100px 1fr;
  }
}

